.header {
    font-size: 4rem;
    font-style: italic;
    padding: 2rem;
    border-bottom-width: 1px;
    border-bottom-style: solid;
}

.header::after {
  content: "";
  background: url('hay.png') no-repeat;
  background-position: 0 50%; 
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;   

}